import datas from '../../commons/js/datas.js'
// import 
export default {
	data() {
		return {
			userarr:[],
			userid:1,
		};
	},
	methods:{
		//获取关键词
		search:function(e){
			this.userarr = [];
			let searchval = e.detail.value
			if(searchval.length>0){this.searchUser(searchval)}
			//console.log(this.userarr)
		},
		//寻找关键词匹配的用户
		searchUser:function(e){
			let arr = datas.friends();
			let exp = eval("/"+e+"/g")
			for(let i in arr){
				if(arr[i].name.search(e) != -1  || arr[i].email.search(e) != -1){
					this.isFriend(arr[i])
					arr[i].imgurl  = '../../static/images/img/'+arr[i].imgurl
					arr[i].name  = arr[i].name.replace(exp,"<span style='color:#4AAAFF;'>"+e+"</span>");
					arr[i].email  = arr[i].email.replace(exp,"<span style='color:#4AAAFF;'>"+e+"</span>");
					this.userarr.push(arr[i])
				}
			}
		},
		//判断是否为好友
		isFriend:function(e){
			let tip = 0;
			let arr = datas.isFriend()
			//console.log(e)
			for(let i in arr){
				if(arr[i].friend == e.id){
					//console.log(arr[i].friend+"   "+e.id)
					tip = 1;
				}
			}
			e.tip = tip
			//console.log(e.tip)
		},
		//返回首页
		backOne:function(){
			uni.navigateBack({
			    delta: 1
			});
		},
	}
}
